import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { createCar, fetchCars } from '../../../../store/Cars/thunks';
import { Dispatcher } from '../../../../types/store';
import { Car, CarsState } from '../../../../types/Cars';
import { CarForm } from '../../../../types/Edit';
import { UseFormSetValue } from 'react-hook-form/dist/types/form';

export const useSaveCar = (form: CarForm | null, carId: string): [boolean] => {
  const dispatch = useDispatch<Dispatcher>();
  const { postCar, postLoading, postError } = useSelector<{
    cars: CarsState;
  }, CarsState>((state) => state.cars);
  const [body, setBody] = useState<object | null>(null);

  useEffect(() => {
    if (form) {
      const formData = new FormData();
      formData.append('name', form.name);
      formData.append('description', form.description);
      formData.append('priceMin', form.priceMin);
      formData.append('priceMax', form.priceMax);
      formData.append('number', form.number);
      formData.append('thumbnail', form.thumbnail[0]);
      form.colors.forEach((color) => {
        formData.append('colors', color);
      });
      formData.append('categoryId', form.categoryId);
      setBody(formData);
    }
  }, [form]);

  useEffect(() => {
    if (body !== null) {
      if (carId) {
        console.log('hm');
      } else {
        dispatch(createCar(body));
      }
    }
  }, [body]);

  return [postLoading];
};

export const useCarById = (carId: string, setValue: UseFormSetValue<CarForm>): [boolean, number] => {
  const dispatch = useDispatch<Dispatcher>();
  const { cars, loading, error } = useSelector<{
    cars: CarsState;
  }, CarsState>((state) => state.cars);

  useEffect(() => {
    dispatch(fetchCars({}, carId));
  }, []);

  useEffect(() => {
    if (cars) {
      const car = cars.data[0];
      setValue('name', car.name);
      setValue('categoryId', car?.categoryId?.id);
      setValue('description', car.description);
      setValue('colors', car.colors);
      setValue('name', car.name);
      const blob = fetch(car.thumbnail.path)
        .then((r) => r.blob())
        .then((blob) => {
          const carImage = new File(blob, car.thumbnail.originalname,)
          setValue('thumbnail', [blob] )
        });
    }
  }, [cars]);

  return [loading, error];
};
